h1, h2, h3, h4, h5 {
	margin: 0;
	padding: 0;
	font-weight: normal;
	font-style: normal;
	word-break: break-word;
    line-height: 1.2;
	/*-webkit-transform: translateZ(0);*/
}

h1, .h1 {
    color: green;

    @media (max-width: 300px) {
        color: red;
    }
}

h2, .h2 {
    @media (max-width: 300px) {
        color: blue;
    }
}

