*, *:before, *:after {
  box-sizing: border-box; }

p, a {
  margin: 0;
  padding: 0;
  /*-webkit-transform: translateZ(0);*/ }

p {
  margin-bottom: 1em; }

a {
  text-decoration: none;
  transition: all .4s;
  color: inherit; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

