/* --------------------------------------------------------------------------- */
/*
/*	headlines
/*
/* --------------------------------------------------------------------------- */
h1, h2, h3, h4, h5 {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-style: normal;
  word-break: break-word;
  line-height: 1.2;
  /*-webkit-transform: translateZ(0);*/ }

h1, .h1 {
  color: green; }
  @media (max-width: 300px) {
    h1, .h1 {
      color: red; } }

@media (max-width: 300px) {
  h2, .h2 {
    color: blue; } }

/* --------------------------------------------------------------------------- */
/*
/*	buttons
/*
/* --------------------------------------------------------------------------- */
.button {
  appearance: none;
  cursor: pointer; }

/* --------------------------------------------------------------------------- */
/*
/*	content
/*
/* --------------------------------------------------------------------------- */
.content a {
  text-decoration: underline; }

.content ul {
  list-style: disc;
  padding-left: 1em;
  margin-bottom: 1em; }
